import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Card, Row, Col, Table, Button, Select } from 'antd';
import { listClassOfficers } from 'dump/data';
import SearchBar from './SearchBar';
import './style.scss';

const { Option } = Select;
const table1 = [
  {
    title: 'Mã SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Chức danh',
    dataIndex: 'CD',
    render: () => (
      <Fragment>
        <Select defaultValue="jack" style={{ width: '100%' }}>
          <Option value="jack">Lớp trưởng</Option>
          <Option value="lucy">Lớp phó</Option>
          <Option value="Yiminghe">Bí thư</Option>
        </Select>
      </Fragment>
    ),
  },
  {
    title: 'Chức danh kim',
    dataIndex: 'CDK',
    render: () => (
      <Fragment>
        <Select defaultValue="lucy" style={{ width: '100%' }}>
          <Option value="jack">Lớp trưởng</Option>
          <Option value="lucy">Lớp phó</Option>
          <Option value="Yiminghe">Bí thư</Option>
        </Select>
      </Fragment>
    ),
  },
];

const table2 = [
  {
    title: 'Mã SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Chức danh',
    dataIndex: 'CD',
    key: 'CD',
  },
  {
    title: 'Chức danh kim',
    dataIndex: 'CDK',
    key: 'CDK',
  },
  {
    title: 'Thao tác',
    dataIndex: 'thaotac',
    render: () => (
      <Fragment>
        <Button type="danger">Xóa</Button>
      </Fragment>
    ),
  },
];

class ListClassOfficers extends Component {
  state = {};

  render() {
    return (
      <Fragment>
        <Card title="Chọn cán bộ lớp">
          <Row>
            <Col span={24}>
              <SearchBar />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table bordered dataSource={listClassOfficers} columns={table1} />
            </Col>
            <Col offset={8} span={12}>
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={this.cancelUpload} />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListClassOfficers />
  </Page>
);



